<template>
    <div class="chart">
        <div class="before-chart">
            <span class="title">Moliya korsatkichlari</span>
            <div class="chart-settings">
                <div>
                    <span class="green_dot"></span>
                    <span>Kirim</span>
                </div>
                <div>
                    <span class="brown_dot"></span>
                    <span>Chiqim</span>
                </div>
                <div>
                    <!-- <v-btn elevation="0" class="btn">
                        <img src="@/assets/images/filter.svg" alt="">
                    </v-btn> -->
                </div>
            </div>
        </div>
        <div id="line-chart" class="mt-5">
            <apexchart ref="chart" type="area" height="500" :options="apexAreaChartProps.chartOptions"
                :series="apexAreaChartProps.series"></apexchart>
        </div>
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import VueApexCharts from 'vue-apexcharts'
// import { priceMixin } from '@/mixins/formatter.js';

export default {
    // mixins: [priceMixin],
    components: {
        apexchart: VueApexCharts
    },
    data() {
        return {
            expenseData: {},
        }
    },
    async mounted(){

    },
    methods: {
        // ============= PRICE_FORMATTER START =============
        formatPrice(value) {
            console.log(value)
            if(value){
                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);
                console.log(currency)
                return currency;
            }
            return 0;
        },
        // ============= PRICE_FORMATTER END =============
    },
    computed: {
        
        apexAreaChartProps() {
            return {
                series: [
                    {
                        name: "Kirim summasi",
                        data: [
                            this.expenseData.januarySalePayments,
                            this.expenseData.februarySalePayments,
                            this.expenseData.marchSalePayments,
                            this.expenseData.aprilSalePayments,
                            this.expenseData.maySalePayments,
                            this.expenseData.juneSalePayments,
                            this.expenseData.julySalePayments,
                            this.expenseData.augustSalePayments,
                            this.expenseData.septemberSalePayments,
                            this.expenseData.octoberSalePayments,
                            this.expenseData.novemberSalePayments,
                            this.expenseData.decemberSalePayments
                        ],
                    },
                    {
                        name: "Chiqim summasi",
                        data: [
                            this.expenseData.januaryExpenses,
                            this.expenseData.februaryExpenses,
                            this.expenseData.marchExpenses,
                            this.expenseData.aprilExpenses,
                            this.expenseData.mayExpenses,
                            this.expenseData.juneExpenses,
                            this.expenseData.julyExpenses,
                            this.expenseData.augustExpenses,
                            this.expenseData.septemberExpenses,
                            this.expenseData.octoberExpenses,
                            this.expenseData.novemberExpenses,
                            this.expenseData.decemberExpenses
                        ],
                    },
                ],
                chartOptions: {
                    chart: {
                        height: 350,
                        type: 'area',
                        toolbar: {
                            show: false
                        },
                        zoom: {
                            enabled: false,
                        }
                    },
                    dataLabels: {
                        enabled: false
                    },
                    colors: ['#43D9A9', '#EBC59E'],
                    stroke: {
                        width: [5, 7, 5],
                        curve: 'smooth',
                    },
                    legend: {
                        // tooltipHoverFormatter: function (val, opts) {
                        //     return val + ' - ' + opts.w.globals.series[opts.seriesIndex][opts.dataPointIndex] + ''
                        // }
                        show: false,
                    },
                    markers: {
                        size: 0,
                        hover: {
                            sizeOffset: 6
                        }
                    },
                    yaxis: {
                        labels: {
                            formatter: function (val) {
                                val = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(val);
                                return val
                            }
                        }
                    },
                    xaxis: {
                        categories: ["Yanvar", "Fevral", "Mart", "Aprel", "May", "Iyun", "Iyul", "Avgust", "Sentabr", "Oktabr", "Noyabr", "Dekabr"]
                    },
                    tooltip: {
                        theme: 'dark',
                        y: [
                            {
                                title: {
                                    formatter: function (val) {
                                        return val
                                    }
                                },
                                formatter: function (val) {
                                    val = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(val);
                                    return val
                                }
                            },
                            {
                                title: {
                                    formatter: function (val, series) {
                                        return val
                                    }   
                                },
                                formatter: function (val) {
                                    val = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(val);
                                    return val
                                }
                               
                            },
                        ],
                    },
                    grid: {
                        borderColor: '#f1f1f1',
                        show: true,
                    },


                },
            }
        }
    },
    async mounted() {
        await this.$axios.get("/dashboard-chart-analytics").then(response => {
            this.expenseData = response.data;
        });
    }
}
</script>

<style lang="scss" scoped>
#line-chart {
    background: #FFFFFF !important;
    // box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px !important;
    box-shadow: 6px 12px 30px rgba(22, 28, 36, 0.04) !important;
    border-radius: 14px;
    width: 100%;
}

.chart {
    padding-top: 20px;
    padding-bottom: 40px;
    border-bottom: 3px solid #F5F5F5;

    .before-chart {
        display: flex;
        justify-content: space-between;

        .chart-settings {
            display: flex;

            div {
                display: flex;
                justify-content: center;
                align-items: center;
                margin: 0 0 0 25px;
                font-size: 14px;
                color: #858D90;

                .btn {
                    box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 5px 1px !important;
                    background: #fff !important;
                }
            }

            .green_dot {
                display: inline-block;
                width: 15px;
                height: 15px;
                background: #43D9A9;
                border-radius: 100%;
                margin-right: 5px;
            }

            .brown_dot {
                display: inline-block;
                width: 15px;
                height: 15px;
                background: #EBC59E;
                border-radius: 100%;
                margin-right: 5px;
            }
        }
    }
}

</style>
