                plotOptions: {
                    pie: {
                        customScale: 1,
                        donut: {
                            size: '50%',
                        },
                        expandOnClick: false
                    }
                },
                labels: ["1-fabrika", "2-fabrika", "3-fabrika", "Umumiy"],
                tooltip: {
                        y: {
                            formatter: function (val) {
                                val = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(val);
                                return val
                            }
                        },
                },
                legend: {
                    show: false,
                }
            },
            options2: {
                colors: ['#43D9A9', '#2886FF', '#EBC59E', '#ff4040', '#8a2be2', '#708090', '#ffcc33', '#ff1493', 'ff1493', '#00ced1'],
                plotOptions: {
                    pie: {
                        customScale: 1,
                        donut: {
                            size: '50%',
                        },
                        expandOnClick: false
                    }
                },
                labels: ["Mato to`quv", "Yoqa to`quv", "Bo`yoqxona", " Umumiy"],
                tooltip: {
                        y: {
                            formatter: function (val) {
                                val = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(val);
                                return val
                            }
                        },
                },
                legend: {
                    show: false,
                }
            },
            series: [],
            series2: [],
            items: [],
            totalFactoriesSum: 0,
            totalSewingsSum: 0,
            count: 0,
            count2: 0,
        }
    },
     async created() {
        await this.refresh()

        await this.$axios.get("/finance-analytics").then(response => {
            this.series2.push(response.data.materialDebtors);
            this.series2.push(response.data.collarDebtors);
            this.series2.push(response.data.paintingDebtors);
            this.totalSewingsSum = response.data.debt;
        });
    },
    watch: {
        // ============= OTHER_WATCHERS START =============
        page: function (newVal, oldVal) {
            if (newVal) {
                this.refresh()
            }
        },
        '$store.state.main.finance_debt_material': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_FINANCE_DEBT_MATERIAL', false)
                }
            },
            deep: true,
            immediate: true
        },
        // ============= OTHER_WATCHERS END =============
    },
    computed: {
        ...mapState({
            dashboard_analytic_factory: state => state.home.dashboard_analytic_factory,
        }),
        computeCircleClasses(){
            this.count += 1;
            return this.colors[this.count - 1]
        },
        computeCircleInnerClasses(){
            this.count2 += 1;
            return this.colors[this.count2 - 1]
        },
    },
    methods: {
        ...mapActions(
            {
                fetchDashboardAnalyticFactory: 'home/fetchDashboardAnalyticFactory'
            }
        ),
        computeBlocks(){
            let result = Math.ceil(this.dashboard_analytic_factory.length / 2)
            return result
        },
        computeFactories(index){
            let start;
            if(index == 0){
                start = index;
            }else{
                start = index * 2;
            }
            let end = (index + 1) * 2;
            let result = this.dashboard_analytic_factory.slice(start, end)
            return result
        },
        computeBlockClasses(index, index2){
            if(index == 0 && index2 == 0){
                return 'first';
            }else if(index == 0 && index2 == 1){
                return 'second';
            }else if(index == 1 && index2 == 0){
                if(this.dashboard_analytic_factory.length > 4){
                    return 'first';
                }else{
                    return 'third'
                }
            }else if(index == 1 && index2 == 1){
                if(this.dashboard_analytic_factory.length > 4){
                    return 'second';
                }else{
                    return 'fourth'
                }
            }else if(index == 2 && index2 == 0){
                if(this.dashboard_analytic_factory.length > 6){
                    return 'first';
                }else{
                    return 'third'
                }
            }else if(index == 2 && index2 == 1){
                if(this.dashboard_analytic_factory.length > 6){
                    return 'second';
                }else{
                    return 'fourth'
                }
            }else if(index == 3 && index2 == 0){
                if(this.dashboard_analytic_factory.length > 8){
                    return 'first';
                }else{
                    return 'third'
                }
            }else if(index == 3 && index2 == 0){
                if(this.dashboard_analytic_factory.length > 8){
                    return 'second';
                }else{
                    return 'fourth'
                }
            }else if(index == 3 && index2 == 0){
                if(this.dashboard_analytic_factory.length > 10){
                    return 'first';
                }else{
                    return 'third'
                }
            }else if(index == 3 && index2 == 0){
                if(this.dashboard_analytic_factory.length > 10){
                    return 'second';
                }else{
                    return 'fourth'
                }
            }
        },  
        



       // ============= REFRESH START =============
        async refresh(){
            this.loading = true
            
            await this.fetchDashboardAnalyticFactory({page: this.page, factory_type: 'material'});
            this.dashboard_analytic_factory.forEach((elem) => {
                this.series.push(elem.debts)
                this.totalFactoriesSum = this.totalFactoriesSum + elem.debts
            })

            this.dashboard_analytic_factory.push({name: 'Umumiy', debts: this.totalFactoriesSum})

            
            this.loading = false
        },
        // ============= REFRESH END =============





        // // ============= CLEARS_AND_CLOSES START =============
        // clearInputs(){
        //     this.date = ''
        //     this.clearErrors()
        // },
        // clearErrors(){
        //     this.errorStatus = false;
        //     this.errorMessage = '';
        //     this.errorPlace = '';
