<template>
  <div class="home">
        <div class="home__heading">
            <h1 class="title">Son korsatkichlar</h1>
            <v-dialog
                v-model="modal"
                persistent
                width="290px"
            >
                <template v-slot:activator="{ on, attrs }">
                    <div class="home__heading__date_filter">
                        <v-text-field
                            dense
                            v-model="date"
                            placeholder="Sana tanlang"
                            append-icon="mdi-calendar"
                            readonly
                            outlined
                            v-bind="attrs"
                            v-on="on"
                            hide-details
                        ></v-text-field>
                    </div>
                </template>
                <v-date-picker
                    v-model="date"
                    scrollable
                    color="blue lighten-1"
                    locale="ru-ru"
                    selected-items-text="2ta Tanlandi"
                    type="month"
                >
                <v-card-actions class="d-flex justify-space-between btn-pickers">
                    <v-btn
                        class="backBtn "
                        elevation="0"
                        @click="closeDialog()"
                    >O`chirish
                    </v-btn>
                    <v-btn
                        class="primaryBtn"
                        elevation="0"
                        @click="FilterByDate()"
                    >Filtirlash
                    </v-btn>
                </v-card-actions>
                </v-date-picker>
            </v-dialog>
        </div>
        <FirstRowCards v-if="$can('read', 'home_first_cards')" ref="first_row_cards"></FirstRowCards>
        <SecondRowCards v-if="$can('read', 'home_second_cards')"></SecondRowCards>
        <Chart v-if="$can('read', 'home_line')"></Chart>
        <Debts v-if="$can('read', 'home_pie')"></Debts>

        <!-- <BottomTables v-if="$can('read', 'home_tables')"></BottomTables> -->
  </div>
</template>

<script>
import {mapActions, mapState} from "vuex";
import FirstRowCards from './FirstRowCards.vue'
import SecondRowCards from './SecondRowCards.vue'
import Chart from './Chart.vue'
import Debts from "./Debts.vue";

export default {
    components:{
        FirstRowCards,
        SecondRowCards,
        Chart,
        Debts,
    },
    data: () => ({
        date: '',
        modal: false,
    }),
    async mounted() {
        await this.refresh()
    },
    computed: {
        ...mapState({
            user: state => state.main.user,
            one_user: state => state.main.one_user,
            profile: state => state.main.profile
        }),
    },
    methods: {
        ...mapActions({
            fetchOneUser: 'main/fetchOneUser'
        }),
        // ============= REFRESH START =============
        async refresh(){
            this.loading = true
            await this.fetchOneUser(this.user.user_id)

            this.profile.photo = this.one_user.image
            this.profile.login = this.one_user.email
            this.profile.fullname = this.one_user.name

            this.loading = false
        },
        // ============= REFRESH START =============

        async closeDialog(){
            this.modal = false;
            this.date = '';

            await this.$axios.get(`/dashboard-analytics`).then(response => {
                this.$refs.first_row_cards._data.analyticsData = response.data;
            });
        },
        async FilterByDate(){
            await this.$axios.get(`/dashboard-analytics?date=${this.date}`).then(response => {
                this.$refs.first_row_cards._data.analyticsData = response.data;
            });
            this.modal = false;
        }
    }

};
</script>

<style lang="css" scoped>
/* .home {
    background: #FBFBFB;
} */
</style>
